import React from "react"
function FormComponent(props){
    return(
        <main>
        <form>
          <input name="firstName" value={props.data.firstName} type="text" placeholder=" First Name" onChange={props.handleChange}/><br/>
          <input name="lastName" value={props.data.lastName} type="text" placeholder="Last Name" onChange={props.handleChange}/><br/>
          <input name="age" value={props.data.age} type="number" placeholder="Your Age" onChange={props.handleChange}/><br/>
           
        <label>
          <input name="gender" value="male" type="radio" checked={props.data.gender==="male"} onChange={props.handleChange}/>Male
        </label>
        <label>
          <input name="gender" value="female" type="radio" checked={props.data.gender==="female"} onChange={props.handleChange}/>Female
        </label>
    
        <select name="destination" value={props.data.destination} onChange={props.handleChange}>
          <option value="syria">Syria</option>
          <option value="sweden">Sweden</option>
          <option value="spain">Spain</option>
          <option value="italy">Italy</option>
        </select><br/>
    
        <label>
          <input name="isVegan" type="checkbox"  onChange={props.handleChange} checked={props.data.isVegan}/>Vegan?
        </label>
        <label>
          <input name="isKosher" type="checkbox"  onChange={props.handleChange} checked={props.data.isKosher}/> isKosher?
        </label>
        <label>
          <input name="isLactoseFree" type="checkbox"  onChange={props.handleChange} checked={props.data.isLactoseFree}/>isLactoseFree?
        </label>
    
        <button>Submit</button>
        </form>
    
         <hr/>
         <h1>Entered Information</h1>
          <h3>Your name: {props.data.firstName} {props.data.lastName}</h3>
           <h3>Your age: {props.data.age}</h3>
           <h3>Your gender: {props.data.gender}</h3>
           <h3>Your destination: {props.data.destination}</h3>
           <h3>Your  dietary Restrictions: {props.data.isKosher? "kosher":null } {props.data.isLactoseFree?"LactoseFree":null}  {props.data.isVegan?"vegan":null}</h3>
        </main>
      )
}

export default FormComponent 